body {
    font-family: $font-primary;
    font-size: $font-size;
    color: $color-secondary;
    background: $color-white;
}

a {
    transition: $ease-0;
    text-decoration: none;

    &:hover {
        text-decoration: underline;
    }
}

a:focus,
button:focus {
    outline: none;
    box-shadow: none;
}

img {
    max-width: 100%;
    height: auto;
}

strong{
    font-weight: 700;
}

.main-content{
    padding-top: 89px;

    @media (max-width: 991px) {
        padding-top: 60px;
    }

    &.internal{
        padding-top: 85px;

        @media (max-width: 991px) {
            padding-top: 60px;
        }
    }

}
